// Imports
import { IExecutable, serial } from 'gulp-core-build';

import { ConfigureWebpackTask } from './configureWebpack/ConfigureWebpackTask';
import { CopyAssetsTask } from './copyAssets/CopyAssetsTask';
import { WriteManifestsTask } from './writeManifests/WriteManifestsTask';
import { CasperJSTask } from './casprJs/CasperJSTask';
import { CopyStaticAssetsTask } from './copyStaticAssets/CopyStaticAssetsTask';
import { CollectLocalizedResourcesTask } from './collectLocalizedResources/CollectLocalizedResourcesTask';
import { PackageSolutionTask } from './packageSolution/PackageSolutionTask';
import { DeployAzureStorageTask } from './deployAzureStorage/DeployAzureStorageTask';

// Exports
export const configureWebpack: ConfigureWebpackTask = new ConfigureWebpackTask();
export const copyAssets: CopyAssetsTask = new CopyAssetsTask();
export const writeManifests: WriteManifestsTask = new WriteManifestsTask();
export const casperJSRunner: CasperJSTask = new CasperJSTask();
export const copyStaticAssets: CopyStaticAssetsTask = new CopyStaticAssetsTask();
export const collectLocalizedResources: CollectLocalizedResourcesTask = new CollectLocalizedResourcesTask();
export const packageSolution: PackageSolutionTask = new PackageSolutionTask();
export const deployAzureStorage: DeployAzureStorageTask = new DeployAzureStorageTask();

// @todo (SPPPlat VSO bug #189028) This is a workaround for a TypeScript compiler bug
// that we reported.  After the compiler is fixed, we will remove the typecast.
/* tslint:disable:no-any */
export const prepareDeploy: IExecutable = serial(collectLocalizedResources as any, copyAssets as any);
/* tslint:disable:no-any */
